<template>
    <section class="section quest">

        <form action="#" ref="quiz" class="container quest__container">

            <div class="quest__wrap quest__wrap--first">
                <h2 class="section__title quest__title"> {{ questions[step].question }} </h2>

                <div v-if="typeRadio()" class="quest__box">

                    <div v-for="(answer,index) in questions[step].answers" :key="index" class="quest__input-group">
                        <label class="quest__label">
                            <input
                                name="quest"
                                type="radio"
                                class="quest__radio"
                                v-model="dataAnswer"
                                @change="setAnswer"
                                :value="answer"
                            >
                            <span class="quest__radio-text"> {{ answer }} </span>
                        </label>
                    </div>

                </div>

                <div v-if="typeCheckbox()" class="quest__box">

                    <div v-for="(answer,index) in questions[step].answers" :key="index" class="quest__input-group">
                        <label class="quest__label">
                            <input
                                name="quest"
                                type="checkbox"
                                class="quest__radio"
                                v-model="dataAnswerArr"
                                :value="answer"
                            >
                            <span class="quest__radio-text"> {{ answer }} </span>
                        </label>
                    </div>

                </div>

                <div v-if="typeInput()" class="quest__box">
                    <div class="quest__input-group">
                        <input
                            :placeholder="questions[step].placeholder"
                            type="text"
                            class="quest__input"
                            :class="{'is-invalid':($v.dataAnswer.$dirty && !$v.dataAnswer.required) || ($v.dataAnswer.$dirty && !$v.dataAnswer.minLength)}"
                            v-model="dataAnswer"
                            id="quiz-input"
                        >
                        <div  class="invalid-feedback" v-if="($v.dataAnswer.$dirty && !$v.dataAnswer.required)"> .</div>
                        <div  class="invalid-feedback" v-else-if="($v.dataAnswer.$dirty && !$v.dataAnswer.minLength)"> 2 </div>
                        <div  class="invalid-feedback" v-else-if="($v.dataAnswer.$dirty && !$v.dataAnswer.numeric)"> </div>
                    </div>
                </div>
            </div>

            <div class="quest__wrap">

                <div class="row quest__button-group">
                    <button v-if="step === 0" @click.prevent="" class="button quest__button button--back"></button>
                    <button v-if="step > 0" @click.prevent="prevNext(-1)" class="button quest__button button--back"></button>
                    <button v-if="step !== questions.length " @click.prevent="prevNext(1)" class="button quest__button"></button>
                </div>

                <div class="quest__progress row">
                    <div v-for="(question, index) in questions.length" class="quest__progress__item" :class="{'quest__progress__item--active' : index == step}"></div>
                </div>
            </div>

        </form>

    </section>
    <!-- /.section quest -->

</template>

<script>

import {minLength, required} from "vuelidate/lib/validators";

export default {
    name: "Quiz",
    data: () => ({
        dataAnswer: '',
        dataAnswerArr: [],
        myShow: true
    }),
    computed:{
        statePrevNextLastStep() {
            return this.$store.getters.statePrevNextLastStep
        },
        quiz() {
          return this.$store.getters.stateQuiz
        },
        questions(){
            return this.$store.getters.getQuestions
        },
        step(){
            return this.$store.getters.getStep
        },
        lastStep(){
            return this.$store.getters.getLastStep
        },
        getDataAnswer(){
            if (this.$store.getters.getDataAnswer[this.step])
                return this.$store.getters.getDataAnswer[this.step].answer
            return false
        }
    },
    methods:{
        numeric() {
            return  this.questions[this.step].numeric
        },
        typeCheckbox() {
            return this.questions[this.step].type === 'checkbox'
        },
        typeRadio() {
            return this.questions[this.step].type === 'radio'
        },
        typeInput() {
            return this.questions[this.step].type === 'input'
        },
        prevNext(val){
            if(this.typeCheckbox()) {
                const object = {
                    question: this.questions[this.step].question,
                    answer: this.dataAnswerArr.join()
                }
                this.$store.dispatch('setData',object)
            }
            if(this.typeInput()) {
                if (this.$v.$invalid) {
                    this.$v.$touch()
                    return
                }
                this.$v.$reset()
                const object = {
                    question: this.questions[this.step].question,
                    answer: this.dataAnswer
                }
                this.$store.dispatch('setData',object)
            }
            this.$store.dispatch('prevNext',val)
            this.dataAnswer = ''
            this.dataAnswerArr = []
            if (this.getDataAnswer && this.questions[this.step].type !== 'checkbox') {
                console.log(1)
                this.dataAnswer = this.getDataAnswer
            } else if (this.getDataAnswer && this.questions[this.step].type === 'checkbox') {
                this.dataAnswerArr = this.getDataAnswer.split(',')
            }
            if (!this.lastStep) {
                this.$refs.quiz.classList.add('blink')
                setTimeout(() => {
                    this.$refs.quiz.classList.remove('blink')
                }, 100)
            }
        },
        setAnswer(){
            const object = {
                question: this.questions[this.step].question,
                answer: this.dataAnswer
            }

            this.$store.dispatch('setData',object)
            this.myShow = false
            setTimeout(()=> {
                this.myShow = true
                this.prevNext(1)
            },500)
        }
    },
    validations: {
        dataAnswer: {
            required,
            minLength: minLength(2),
            numeric: function (dataAnswer) {
                if (this.numeric()) {
                    const notDigits = /\D/
                    if (notDigits.test(dataAnswer)) {
                        return false
                    }
                }
                return true
            }
        }
    },
    created() {
        if(this.$store.getters.statePrevNextLastStep) {
            this.dataAnswer = this.$store.getters.getDataAnswer[this.step].answer
        }
    }
}
</script>

<style scoped>
@keyframes blinker {
    from { opacity: 0; }
    to { opacity: 1; }
}
.blink {
    animation: blinker 0.5s;
}
.close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
}
.close:hover {
    opacity: 1;
}
.close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #333;
}
.close:before {
    transform: rotate(45deg);
}
.close:after {
    transform: rotate(-45deg);
}

.is-invalid{
    border:1px solid #dc3545;
    color: #dc3545;
}
.invalid-feedback{
    position:absolute;
    top: 0;
    left: 5px;
    width: 100%;
    color: #dc3545;
    text-align: left;
    font-size: 12px;
}
</style>
