export default {
    state:{
        questionAnswer:[
            {
                question : '    ?',
                type: 'input',
                placeholder: '  '
            },
            {
                type: 'radio',
                question: '    ?',
                answers: [
                    '',
                    ' ',
                    ' '
                ]
            },
            {
                question : '  ',
                type: 'input',
                numeric: true,
                placeholder: '  '
            },
            {
                question : '     ?',
                type: 'radio',
                answers: [
                    ' 6 ',
                    ' 6   12 ',
                    '1-3 ',
                    '3-5 ',
                    ' 5 '
                ]
            },
            {
                question : '    ?',
                type: 'checkbox',
                answers: [
                    ' ',
                    '',
                    '',
                    ''
                ]
            },
            {
                question : '     ,   ?',
                type: 'radio',
                answers: [
                    ', /',
                    ',  ',
                    ', ',
                    ''
                ]
            },
            {
                question : '       ?',
                type: 'radio',
                answers: [
                    ',  ',
                    '',
                    ',   '
                ]
            }
        ],
        quiz: false,
        data: [],
        step: 0,
        lastStep: false,
        prevNextLastStep: false
    },
    getters: {
        statePrevNextLastStep(state) {
            return state.prevNextLastStep
        },
        stateQuiz(state) {
            return state.quiz
        },
        getQuestions(state){
            return state.questionAnswer
        },
        getStep(state){
            return state.step
        },
        getLastStep(state){
            return state.lastStep
        },
        getDataAnswer(state){
            return state.data
        }
    },
    mutations: {
        prevNextLastStepTrue(state) {
            state.prevNextLastStep = true
        },
        prevNextLastStepFalse(state) {
            state.prevNextLastStep = false
        },
        quizTrue(state) {
          state.quiz = true
        },
        changeStep(state,payload){
            state.step += payload
        },
        set(state,payload){
            state.data[state.step] = payload
            // console.log(state.data)
        },
        closeLastStep(state){
            state.lastStep = false
        },
        clearData(state){
            state.data = []
            state.step = 0
        }
    },
    actions: {
        prevNextLastStepTrue ({state, commit}) {
            commit('prevNextLastStepTrue')
        },
        prevNextLastStepFalse ({state, commit}) {
            commit('prevNextLastStepFalse')
        },
        prevNext({state, commit}, value) {
            console.log(state.data)
            if (value <= 0) {
                commit('changeStep', value)
            } else {
                if (!state.data[state.step] || state.data[state.step].answer === '') {
                    alert('  !')
                    return false
                }
                if (state.step === state.questionAnswer.length - 1) {
                    state.lastStep = true
                    state.quiz = false
                    return false
                }
                commit('changeStep', value)
            }

        },
        setData({commit,state}, value) {
            commit('set', value)
        },
        closeLastStep({commit}){
            commit('closeLastStep')
        },
        clearData({commit}){
            commit('clearData')
        }
    }

};
