<template>

    <section v-if="lastStep" class="section last">

        <form @submit.prevent="addOrder" action="#" class="container last__container">
            <div class="last__box">
                <h3 class="section__title last__title">! <br>     !       </h3>
                <img src="img/pictures/img-last-mob.png" alt="" class="last__img">
                <div class="last__block">

                    <div class="last__input-group">
                        <input
                            v-model="name"
                            :class="{'is-invalid':($v.name.$dirty && !$v.name.required) || ($v.name.$dirty && !$v.name.minLength)}"
                            id="quiz-name"
                            type="text"
                            placeholder="  "
                            class="last__input"
                        >
                        <div  class="invalid-feedback" v-if="($v.name.$dirty && !$v.name.required)"> .</div>
                        <div  class="invalid-feedback" v-else-if="($v.name.$dirty && !$v.name.minLength)">    1 </div>
                    </div>

                    <div class="last__input-group">
                        <input
                            v-model="phone"
                            v-phone
                            maxlength="16"
                            :class="{'is-invalid':($v.phone.$dirty && !$v.phone.required) || ($v.phone.$dirty && !$v.phone.isPhone)}"
                            :disabled="isDisabled"
                            id="quiz-phone"
                            type="tel"
                            placeholder="  "
                            class="last__input"
                        >
                        <div  class="invalid-feedback" v-if="($v.phone.$dirty && !$v.phone.required)"> .</div>
                        <div  class="invalid-feedback" v-else-if="($v.phone.$dirty && !$v.phone.isPhone)">  </div>
                    </div>
                </div>
                <div class="row last__button-group">
                    <button @click.prevent="prevNext(0)" class="button button--back last__button"></button>

                    <button
                        id="quiz-send"
                        class="button last__button"
                        type="submit"
                    >
                        {{ isDisabled ? '...' : ''}}
                    </button>
                </div>
            </div>
        </form>

    </section>
    <!-- /.section last -->

</template>

<script>
import {minLength, required} from "vuelidate/lib/validators";

export default {
    name: "LastStep",
    data:() => ({
        phone: '',
        name: '',
        isDisabled: false,
        counters: null,
    }),
    computed:{
        getData(){
            return this.$store.getters.getDataAnswer
        },
        lastStep(){
            return this.$store.getters.getLastStep
        }
    },
    methods:{
        prevNext(val){
            this.$store.dispatch('closeLastStep')
            this.$store.commit('quizTrue')
            this.$store.dispatch('prevNextLastStepTrue')
            this.$store.dispatch('prevNext',val)
        },
        clearData() {
            this.name = '';
            this.phone = '';
        },
        addOrder(){
            if (this.$v.$invalid) {
                this.$v.$touch()
                return
            }
            this.isDisabled = true
            axios.post('/api/order-quiz',{
                name: this.name,
                phone: this.phone.replace(/\D/g, ""),
                data: this.getData,
                host: window.location.host,
                referrer: document.referrer,
                url_query_string: sessionStorage.getItem('urlQueryString'),
            })
                .then( response => {
                    console.log(response)
                    this.clearData()
                    this.$v.$reset()
                    this.isDisabled = false
                    if(response.status === 200) {
                        this.$store.dispatch('clearData')
                        this.$store.dispatch('prevNextLastStepFalse')
                        this.$store.dispatch('closeLastStep')
                        setTimeout( () => this.$store.dispatch('viewThanks') , 500)
                        if (this.counters.ym) ym(this.counters.ym,'reachGoal','order')
                        if (this.counters.fbq) fbq('track', 'Contact')
                        if (this.counters.google) gtag('event', 'conversion', {'send_to': this.counters.google })
                    }
                })
                .catch( error => {
                    this.isDisabled = false
                    console.log(error.message)
                })
        }
    },
    validations: {
        name: { required , minLength: minLength(2)},
        phone: {
            required,
            isPhone: (phone)=> {
                const phoneRe = /^(8|7)[\d]{10}$/;
                if (phone){
                    const digits = phone.replace(/\D/g, "");
                    return phoneRe.test(digits)
                }
                return false
            }
        }
    },
    created() {
        axios.get('/api/counters').then(response => {
            this.counters = response.data
        }).catch( error => {
            console.log(error.message)
        })
    }
}
</script>

<style scoped>
.last__input-group {
    position: relative;
}
.is-invalid{
    border:1px solid #dc3545;
    color: #dc3545;
}
.invalid-feedback{
    position:absolute;
    top: 0;
    left: 2px;
    width: 100%;
    color: #dc3545;
    text-align: left;
    font-size: 14px;
}
</style>
