<template>
    <div v-show="stateThanks" @click="closeThanksTarget" class="modal modal-thanks modal--active">
        <div class="modal-thanks__dialog">
            <img @click="closeThanks" src="img/logo/close.png" alt="close" class="modal-thanks__close">
            <h3 class="section__title modal-thanks__title">  !</h3>
            <p class="modal-thanks__text">       </p>
            <img src="img/logo/logo-last.png" alt="ok" class="modal-thanks__img">
        </div>
    </div>
</template>

<script>
export default {
    name: "ModalThanks",
    methods: {
        closeThanks() {
            this.$store.dispatch('closeThanks')
        },
        closeThanksTarget(e) {
            if (e.target.classList.contains('modal-thanks')) {
                this.$store.dispatch('closeThanks')
            }
        }
    },
    computed: {
        stateThanks() {
            return this.$store.getters.stateThanks
        }
    }
}
</script>

<style scoped>
.overlay {
    overflow: auto;
}
</style>
