export default ({
    state:{
        modal: false,
        thanks: false
    },
    getters:{
        stateThanks(state){
            return state.thanks;
        },
        stateModal(state){
            return state.modal;
        }
    },
    mutations:{
        thanksTrue(state){
            state.thanks = true;
        },
        thanksFalse(state){
            state.thanks = false;
        },
        modalTrue(state){
            state.modal = true;
        },
        modalFalse(state){
            state.modal = false;
        }
    },
    actions:{
        viewThanks({commit}){
            commit('thanksTrue');
            let div = document.createElement('div'),
                scrollWidth;

            div.style.width = '50px';
            div.style.height = '50px';
            div.style.overflowY = 'scroll';
            div.style.visibility = 'hidden';

            document.body.appendChild(div);
            scrollWidth = div.offsetWidth - div.clientWidth;
            div.remove();
            document.body.style.overflow = 'hidden';
            document.body.style.marginRight = `${scrollWidth}px`;
        },
        closeThanks({commit}){
            commit('thanksFalse');
            document.body.style.overflow = '';
            document.body.style.marginRight = '0px';
        },
        viewModal({commit}){
            commit('modalTrue');
            let div = document.createElement('div'),
                scrollWidth;

            div.style.width = '50px';
            div.style.height = '50px';
            div.style.overflowY = 'scroll';
            div.style.visibility = 'hidden';

            document.body.appendChild(div);
            scrollWidth = div.offsetWidth - div.clientWidth;
            div.remove();
            document.body.style.overflow = 'hidden';
            document.body.style.marginRight = `${scrollWidth}px`;
        },
        closeModal({commit}){
            commit('modalFalse');
            document.body.style.overflow = '';
            document.body.style.marginRight = '0px';
        }
    }
});
