<template>

    <div @click="closeModalTarget" v-show="stateModal" class="modal modal-main modal--active">

        <form @submit.prevent="addOrder" action="#" class="modal-main__dialog">
            <img @click="closeModal" src="img/logo/close.png" alt="close" class="modal-main__close">
            <h3 class="section__title modal-main__title"> </h3>
            <p class="modal-main__text">      </p>
            <div class="modal-main__box">

                <div class="modal-main__input-group">
                    <input
                        v-model="name"
                        class="modal-main__input"
                        :class="{'is-invalid':($v.name.$dirty && !$v.name.required) || ($v.name.$dirty && !$v.name.minLength)}"
                        id="modal-name"
                        type="text"
                        placeholder="  "
                        name="name"
                    >
                    <div  class="invalid-feedback" v-if="($v.name.$dirty && !$v.name.required)"> .</div>
                    <div  class="invalid-feedback" v-else-if="($v.name.$dirty && !$v.name.minLength)">    1 </div>
                </div>

                <div class="modal-main__input-group">
                    <input
                        v-model="phone"
                        v-phone
                        maxlength="16"
                        class="modal-main__input"
                        :class="{'is-invalid':($v.phone.$dirty && !$v.phone.required) || ($v.phone.$dirty && !$v.phone.isPhone)}"
                        :disabled="isDisabled"
                        id="modal-phone"
                        type="tel"
                        placeholder="  "
                        name="phone"
                    >
                    <div  class="invalid-feedback" v-if="($v.phone.$dirty && !$v.phone.required)"> .</div>
                    <div  class="invalid-feedback" v-else-if="($v.phone.$dirty && !$v.phone.isPhone)">  </div>
                </div>

                <div class="modal-main__input-group">
                    <input
                        v-model="region"
                        class="modal-main__input"
                        :class="{'is-invalid':($v.region.$dirty && !$v.region.required) || ($v.region.$dirty && !$v.region.minLength)}"
                        id="modal-name"
                        type="text"
                        placeholder="   "
                        name="region"
                    >
                    <div  class="invalid-feedback" v-if="($v.region.$dirty && !$v.region.required)"> .</div>
                    <div  class="invalid-feedback" v-else-if="($v.region.$dirty && !$v.region.minLength)"> 2 </div>
                </div>
            </div>

            <button
                id="modal-send"
                class="button modal-main__button"
                type="submit"
            >
                {{ isDisabled ? '...' : ' '}}
            </button>
        </form>

    </div>
    <!-- /.modal modal--active modal-main -->
</template>

<script>
import {minLength, required} from "vuelidate/lib/validators";

export default {
    name: "Modal",
    data:() => ({
        phone: '',
        name: '',
        region: '',
        isDisabled: false,
        counters: null,
    }),
    computed:{
        getData(){
            return this.$store.getters.getDataAnswer
        },
        stateModal() {
            return this.$store.getters.stateModal
        }
    },
    methods:{
        closeModal() {
            this.$store.dispatch('closeModal')
        },
        closeModalTarget(e) {
            if (e.target.classList.contains('modal-main')) {
                this.$store.dispatch('closeModal')
            }
        },
        clearData() {
            this.name = '';
            this.phone = '';
            this.region = '';
        },
        addOrder(){
            if (this.$v.$invalid) {
                this.$v.$touch()
                return
            }
            this.isDisabled = true
            axios.post('/api/order',{
                name: this.name,
                region: this.region,
                phone: this.phone.replace(/\D/g, ""),
                host: window.location.host,
                referrer: document.referrer,
                url_query_string: sessionStorage.getItem('urlQueryString'),
            })
                .then( response => {
                    console.log(response)
                    this.clearData()
                    this.$v.$reset()
                    this.isDisabled = false
                    if(response.status === 200) {
                        this.$store.dispatch('clearData')
                        this.closeModal()
                        setTimeout( () => this.$store.dispatch('viewThanks') , 500)
                        if (this.counters.ym) ym(this.counters.ym,'reachGoal','order')
                        if (this.counters.fbq) fbq('track', 'Contact')
                        if (this.counters.google) gtag('event', 'conversion', {'send_to': this.counters.google })
                    }
                })
                .catch( error => {
                    this.isDisabled = false
                    console.log(error.message)
                })
        }
    },
    validations: {
        name: { required , minLength: minLength(2)},
        region: { required , minLength: minLength(2)},
        phone: {
            required,
            isPhone: (phone)=> {
                const phoneRe = /^(8|7)[\d]{10}$/;
                if (phone){
                    const digits = phone.replace(/\D/g, "");
                    return phoneRe.test(digits)
                }
                return false
            }
        }
    },
    created() {
        axios.get('/api/counters').then(response => {
            this.counters = response.data
        }).catch( error => {
            console.log(error.message)
        })
    }
}
</script>

<style scoped>
.modal-main__input-group {
    position: relative;
}
.is-invalid{
    border:1px solid #dc3545;
    color: #dc3545;
}
.invalid-feedback{
    position:absolute;
    top: 0;
    left: 2px;
    width: 100%;
    color: #dc3545;
    text-align: left;
    font-size: 14px;
}
</style>
